.footer {
  color: #fff;
  text-transform: uppercase;
  @include fluidFontSize(14px);
  &__top {
    padding-top: 60px;
    padding-bottom: 80px;
    background-color: #223441;
  }

  &__bottom {
    background-color: #151515;
  }
  &__title {
    margin-bottom: 25px;
  }

  &__social {
    margin-top: 30px;
    margin-left: -20px;
    & > * {
      padding-left: 20px;
    }

    a {
      margin-bottom: 10px;
    }

    a:hover {
      opacity: 0.5;
    }
  }

  &__copyright {
    color: #6D6D6D;
    margin-bottom: 0;
    padding-top: 12px;
    padding-bottom: 11px;
    text-align: center;
  }
}

.f-menu {

  &__list {
    column-count: 2;
    margin-top: -10px;
  }

  &__link {
    display: block;
    padding-top: 10px;
    padding-bottom: 11px;

    &:hover {
      color: rgba(#fff, 0.7);
    }
  }
}

@media (max-width: $breakpoint-small) {

  .footer {


    &__top {
      padding-top: 40px;
      padding-bottom: 60px;
    }
  }

  .f-menu {
    padding-bottom: 10px;
    &__list {
      column-count: unset;
    }
  }
}
