.p-rooms {
  margin-bottom: 100px;
  &__el {
    padding-bottom: 20px;
  }
  &__img {
    position: relative;
    margin-bottom: 40px;

    img {
      width: 100%;
    }
  }

  &__tag {
    position: absolute;
    top: 0;
    left: 0;
    padding: 13px 25px;
    background: #fff;
    color: #D70C0C;
    text-align: center;
    text-transform: uppercase;
    text-decoration: underline;
    @include fluidFontSize(14px);
  }

  &__wrap {
    margin-bottom: 20px;
    display: flex;
    align-items: baseline;
    justify-content: space-between;
  }

  &__wrap-price {
    margin: 0;
    color: $g-col-title;
  }

  &__price {
    margin-right: 5px;
    @include fluidFontSize(26px, 32px);
    font-weight: 700;
  }

  &__icons {
    img {
      margin-right: 15px;
    }
  }

  &__title {
    @include fluidFontSize(18px);
    font-weight: 500;
    color: $g-col-title;
  }

  &__text {
    line-height: 1.5;
  }
}

.filter {
  width: 100%;
  padding-top: 20px;
  padding-bottom: 30px;
  padding-left: 20px;
  padding-right: 10px;
  background-color: #F2F4F4;

  &__el {
    display: flex;
    align-items: center;
    padding-top: 10px;
    padding-bottom: 10px;
    color: $g-col-title;
  }

  &__input {
    position: relative;
    display: inline-block;
    padding: 0;
    height: 16px;
    width: 16px;
    margin-left: 0;
    margin-top: 0;
    margin-right: 10px;
    overflow: hidden;
    -webkit-appearance: none;
    -moz-appearance: none;
    vertical-align: middle;
    background-color: transparent;
    border: 1px solid $g-col-title;
    border-radius: unset;

    &:checked:before {
      position: absolute;
      content: "";
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 10px;
      height: 10px;
      background: $g-col-title;
    }
  }

  &__popup {
    position: static;
    padding: 0;
    display: block;
    background: transparent;
    box-shadow: none;
  }
}


.block-text {
  padding-top: 60px;
  padding-bottom: 125px;
  background-color: #F2F4F4;

  &__title {
    margin-bottom: 30px;
    @include fluidFontSize(20px);
    font-weight: 500;
    color: $g-col-title;
  }

  &__text {
    line-height: 1.5;
  }
}

@media (max-width: $breakpoint-medium) {
  .filter {
    padding: 0;

    &__btn {
      padding: 0 15px;
      text-align: left;
      width: 100%;
      border: 1px solid $g-col-extra;
      background-color: #fff;
      height: 50px;
      color: $g-col-title;

      &:after {
        right: 15px;
        border-color: $g-col-extra;
      }
    }

    &__popup {
      position: absolute;
      padding-top: 10px;
      padding-left: 15px;
      padding-right: 15px;
      padding-bottom: 15px;
      display: none;
      background-color: #fff;
      border: 1px solid $g-col-extra;
      border-top: none;
    }
  }
}

@media (max-width: $breakpoint-small) {
  .p-rooms {
    &__img {
      margin-bottom: 15px;
    }

    &__wrap {
      margin-bottom: 15px;
    }

    &__title {
      margin: 15px 0;
    }

    &__text {
      margin-top: 15px;
    }
  }

  .block-text {
    padding-top: 40px;
    padding-bottom: 50px;

    &__title {
      margin-bottom: 20px;
    }
  }
}