.gallery {
  margin-top: 90px;
  margin-bottom: 60px;

  &__title {
    margin-bottom: 30px;
    @include fluidFontSize(20px, 40px);
    font-weight: 500;
    color: $g-col-title;
  }

  &__text {
    line-height: 1.5;
  }
}

@media (max-width: $breakpoint-medium) {
  .gallery {
    margin-top: 40px;
  }
}

@media (max-width: $breakpoint-small) {
  .gallery {
    margin-top: 20px;

    &__title {
      margin-bottom: 10px;
    }
  }
}