.blog {

  &__el {
    padding-bottom: 10px;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  &__img {
    position: relative;
    margin-bottom: 30px;

    img {
      width: 100%;
    }
  }

  &__title {
    color: $g-col-title;
    @include fluidFontSize(18px);
    font-weight: 500;
  }

  &__text {
    min-height: 42px;
    margin-top: 0;
    @include fluidFontSize(14px);
    line-height: 1.5;
  }

  &__tag {
    position: absolute;
    top: 0;
    left: 0;
    margin: 0;
    background-color: #fff;
    padding: 8px 7px 10px;
    color: #EA1515;
    font-weight: 700;
    @include fluidFontSize(18px, 24px);
    text-transform: uppercase;
  }

  .btn_dark {
    margin-top: 40px;
    margin-left: auto;
    margin-right: auto;
    height: 50px;
  }

  &__grid {
    display: grid;
    grid-template-columns: repeat(4, calc(25% - 15px));
    grid-template-rows: repeat(2, auto);
    grid-row-gap: 40px;
    grid-column-gap: 20px;
  }

  &__grid-el {
    &:nth-child(1) {
      grid-column: 1/ 3;
      grid-row: 1 / 3;

      .blog__el {
        padding: 0;
        position: relative;
      }

      .blog__img {
        margin-bottom: 0;
      }

      .blog__title {
        @include fluidFontSize(20px, 30px);
        position: absolute;
        padding-bottom: 60px;
        padding-left: 30px;
        padding-right: 30px;
        max-width: 510px;
        width: 100%;
        top: 30%;
        color: #fff;
        left: 50%;
        transform: translateX(-50%);
        text-align: center;
        &:before {
          position: absolute;
          content: "";
          bottom: 0;
          left: 50%;
          transform: translateX(-50%);
          width: 65%;
          height: 1px;
          background-color: #fff;
        }
      }

      .blog__text {
        position: absolute;
        top: 57%;
        padding: 0 30px;
        max-width: 510px;
        @include fluidFontSize(16px, 14px);
        width: 100%;
        text-align: center;
        color: #fff;
        left: 50%;
        transform: translateX(-50%);
      }
    }

    &:nth-child(2) {
      grid-column: 3 / 5;
      grid-row: 1 / 3;

      .blog__el {
        display: block;
      }
    }

    &:nth-child(7) {
      grid-column: 1 / 3;
      grid-row: 4 / 6;

      .blog__el {
        display: block;
      }
    }

    
  }
}

@media (max-width: $breakpoint-large) {

  .blog {
    &__grid-el {
      &:nth-child(1) {
        .blog__text {
          top: 60%;
        }
      }
    }
  }
}

@media (max-width: $breakpoint-medium) {

  .blog {
    &__grid {
      display: flex;
      flex-wrap: wrap;
      margin-left: -20px;
    }

    &__grid-el {
      margin-bottom: 30px;
      width: 50%;
      padding-left: 20px;

      &:nth-child(1) {
        .blog__title {
          margin: 0;
          top: 30px;
          padding-bottom: 20px;

        }

        .blog__text {
          padding: 0 10px;
          top: 120px;
        }
      }
    }
  }
}

@media (max-width: $breakpoint-small) {

  .blog {

    &__grid {
      display: block;
    }

    &__grid-el {
      width: 100%;

      &:nth-child(1) {

        .blog__img {
          // display: flex;

          img {
            // object-fit: cover;
            min-height: 350px;
          }
        }
      }
    }
    &__el {
      padding-bottom: 0;
    }
    &__text {
      margin-bottom: 0;
      min-height: auto;
    }

    &__img {
      margin-bottom: 15px;
    }

    &__title {
      margin-top: 0;
      margin-bottom: 10px;
    }
  }

}