.img-slider {

  &__slideshow {
    position: relative;
    overflow: hidden;

    &:hover {
      .img-slider__nav {
        transform: none;
      }
    }
  }

  &__wrap-nav {

  }

  &__nav {
    width: 50px;
    height: 50px;
    position: absolute;
    bottom: calc(15% + 110px);
    background-color: #fff;

    &:after {
      border-color: $g-col-main;
      padding: 5px;
    }
  }

  &__nav_left {
    transform: translateX(-100%);
    left: 0;

    &:after {
      left: 40%;
    }
  }

  &__nav_right {
    transform: translateX(100%);
    right: 0;

    &:after {
      right: 40%;
    }
  }

  &__thumbnav {
    margin-top: 30px;
    // overflow-x: scroll;
    padding-bottom: 30px;

    // &::-webkit-scrollbar {
    //   height: 5px;
    // }
    // &::-webkit-scrollbar-track {
    //   background: red;
    // }
  
    // &::-webkit-scrollbar-thumb {
    //   background-color: #322F33;
    //   border-radius: 5px;
    // }

    .uk-slider-items > * {
      display: flex;
      width: 80px;
      height: 80px;
      margin-right: 20px;
      cursor: pointer;
      opacity: 0.5;

      &.uk-active {
        opacity: 1;
      }
      img {
        object-fit: cover;
      }
    }
  }
}

@media (max-width: $breakpoint-small) {

  .img-slider {

    &__controls {
      margin-top: 10px;
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
    &__nav {
      position: relative;
      bottom: 0;

      border: 1px solid $g-col-main;
      transform: none;
    }

    &__nav_right {
      margin-left: 20px;
    }

    &__wrap-dots {

      counter-reset: dot-num;
      padding-left: 20px;
      @include fluidFontSize(16px, 32px);
      color: #A4A4A4;
      &:after {
        content: "/" counter(dot-num);
      }
  
      a {
        display: none;
      }
  
      li {
        position: absolute;
        left: 0;
        padding-left: 0;
        &:before {
          visibility: hidden;
          opacity: 0;
          counter-increment: dot-num;
          content: counter(dot-num);
          transition: $g-anime;
        }
  
        &.uk-active {
          &:before {
            visibility: visible;
            opacity: 1;
          }
        }
      }
    }
  }
}