// banner

.m-banner {
  position: relative;
  &__img {
    position: relative;
    img {
      width: 100%;
    }

    &:before {
      position: absolute;
      content: "";
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: linear-gradient(180deg, rgba(14, 52, 56, 0.45) -45%, rgba(14, 52, 56, 0.3) 100%);
    }
  }

  &__wrap {
    width: 100%;
    padding-left: 30px;
    padding-right: 30px;
    position: absolute;
    top: 20%;
    left: 50%;
    transform: translateX(-50%);
    color: #fff;
    text-align: center;
  }

  &__title {
    margin-bottom: 50px;
    @include fluidFontSize(32px, 64px);
    font-weight: 500;
    color: inherit;
  }

  &__text {
    @include fluidFontSize(14px, 18px);
  }

  &__fm {
    position: absolute;
    padding-left: 70px;
    padding-right: 70px;
    padding-top: 25px;
    padding-bottom: 40px;
    bottom: 20px;
    left: 50%;
    transform: translateX(-50%);
    background-color: #fff;
  }
}

.book-fm {

  &_room {
    margin-bottom: 45px;

    // .book-fm__input {
    //   border-color: $g-col-title;
    //   color: $g-col-title;
    // }

    // .book-fm__btn {
    //   border-color: $g-col-title;
      
    //   &:before {
    //     color: $g-col-title;
    //   }

    //   &:hover {
    //     background-color: $g-col-title;
    //   }

    // }
    
    .book-fm__input_pers {
      width: 50px;
    }

    .btn_dark {
      width: 180px;
      text-transform: uppercase;
      // background-color: $g-col-title;

      // &:hover {
      //   background-color: rgba($g-col-title, 0.7);
      // }
    }
  }

  &__form {
    display: flex;
    align-items: flex-end;
  }

  &__el {
    &:nth-child(-n + 2) {
      max-width: 180px;
    }

    &:nth-child(2) {
      margin-left: 20px;
    }

    &:nth-child(n + 3) {
      margin-left: 25px;
    }
  }

  &__label {
    display: inline-block;
    margin-bottom: 3px;
    @include fluidFontSize(14px);
  }

  &__input {
    padding-left: 15px;
    padding-right: 10px;
    height: 50px;
    @include fluidFontSize(18px);
    border-color: $g-col-extra;
    border-radius: unset;
    color: $g-col-extra;
    background-color: #fff;

    &::-webkit-clear-button {
      display: none;
      -webkit-appearance: none;
    }
  }

  &__pers {
    display: flex;
    align-items: center;
  }

  &__input_pers {
    width: 65px;
    border-left: none;
    border-right: none;
    text-align: center;
    padding-left: 5px;
    padding-right: 5px;
  }

  &__btn {
    position: relative;
    width: 50px;
    height: 50px;
    font-weight: 300;
    @include fluidFontSize(40px);
    border: 1px solid $g-col-extra;
    background-color: #fff;
    &:before {
      position: absolute;
      content: "-";
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      @include fluidFontSize(30px);
      color: $g-col-extra;
      transition: $g-anime;
    }

    &:hover {
      color: #fff;
      background-color: $g-col-extra;

      &::before {
        color: #fff;
      }
    }
  }

  &__btn_plus {
    &:before {
      content: "+";
    }
  }


  .btn_dark {
    height: 50px;
  }
}

@media (max-width: $breakpoint-large) {

  .m-banner {

    &__fm {
      position: static;
      transform: none;
      padding-left: 20px;
      padding-right: 20px;
    }
  }

  .book-fm {

    &_room {
      
      .book-fm__form {
        max-width: 400px;
        justify-content: flex-start;
        flex-wrap: wrap;
        margin-left: -20px;
      }
      .book-fm__el {
        max-width: 200px;
        padding-left: 20px;
        &:nth-child(n + 1) {
          margin-left: 0;
        }
      }

      .book-fm__input_pers {
        width: 80px;
      }
    }

    &__form {
      justify-content: center;
    }
  }
}

@media (max-width: $breakpoint-medium) {

  .book-fm {

    &_room {
      margin-bottom: 30px;
      .book-fm__form {
        display: flex;
      }
      .book-fm__el {

        &:nth-child(n + 1) {
          max-width: 100%;
          width: 200px;
        }
      }
    }

    &__form {
      display: block;
    }

    &__el {

      &:nth-child(n + 1) {
        max-width: 280px;
        margin-left: auto;
        margin-right: auto;
      }

      &:last-child {
        margin-top: 30px;
      }

      margin-bottom: 10px;
    }

    &__input_pers {
      width: 100%;
    }

    &__btn {
      max-width: 50px;
      width: 100%;
    }

    .btn_dark {
      margin-left: auto;
      margin-right: auto;
    }
  }
}

@media (max-width: $breakpoint-small) {
  
  .m-banner {
    padding-bottom: 80px;
    &__img {
      display: flex;
      height: 360px;
      img {
        object-fit: cover;
      }
    }

    &__fm {
      padding-bottom: 0;
    }
  }

  .book-fm {
    &_room {
      margin-bottom: 0;
      .book-fm__form {
        max-width: 280px;
        display: block;
        margin-left: auto;
        margin-right: auto;
      }
      .book-fm__el {
        padding-left: 0;
        &:nth-child(n + 1) {
          width: 280px;
        }
      }
      .book-fm__input_pers {
        width: 180px;
      }

      .btn_dark {
        width: 240px;
      }
    }
  }
}

