// block slider 
.b-slider {

  &_dark {
    background-color: #223441;

    .b-slider__title {
      color: #fff;
    }

    .b-slider__nav {
      border-color: #fff;

      &:after {
        border-color: #fff;
      }

      &:hover {
        background-color: #fff;

        &:after {
          border-color: #223441;
        }
      }
    }
  }

  padding-top: 90px;
  padding-bottom: 110px;

  &__title {
    margin-bottom: 30px;
    @include fluidFontSize(20px, 40px);
    font-weight: 500;
    color: $g-col-title;
  }

  &__content {
    position: relative;
  }

  &__wrap-nav {
    position: absolute;
    display: flex;
    top: 0;
    right: 0;
  }

  &__nav {
    padding: 0;
    width: 48px;
    height: 48px;
    border: 1px solid $g-col-main;

    &:after {
      padding: 5px;
    }

    &:hover {
      background-color: $g-col-main;

      &:after {
        border-color: #fff;
      }
    }
  }

  &__nav_prev {
    &:after {
      left: 45%;
    }
  }

  &__nav_next {
    margin-left: 20px;

    &:after {
      right: 40%;
    }
  }

  &__wrap-dots {
    position: absolute;
    top: 7px;
    right: 150px;
    counter-reset: dot-num;
    padding-left: 20px;
    @include fluidFontSize(16px, 32px);
    color: #A4A4A4;
    &:after {
      content: "/" counter(dot-num);
    }

    a {
      display: none;
    }

    li {
      position: absolute;
      left: 0;
      padding-left: 0;
      &:before {
        visibility: hidden;
        opacity: 0;
        counter-increment: dot-num;
        content: counter(dot-num);
        transition: $g-anime;
      }

      &.uk-active {
        &:before {
          visibility: visible;
          opacity: 1;
        }
      }
    }
  }

  &__link {
    display: block;
    border: none;
    background: transparent;
    text-align: center;
    @include fluidFontSize(18px);
    font-weight: 500;
    color: $g-col-title;
    text-decoration: underline;

    &:hover {
      color: $g-col-title;
      text-decoration: none;
    }
  }
}

// slider room
.s-room {

  &:hover {

    .s-room__img {
      &:before, &:after {
        width: 35%;
      }
    }
    .s-room__filter {
      background: rgba(0, 0, 0, 0.4);
    }

    .s-room__more {
      opacity: 1;
      visibility: visible;
      border-color: #fff;
    }
  }

  &__img {
    position: relative;

    &:before, &:after {
      position: absolute;
      content: "";
      z-index: 5;
      top: 50%;
      left: 0;
      width: 0;
      height: 1px;
      background-color: #fff;
      transition: $g-anime;
    }

    &:after {
      left: auto;
      right: 0;
    }
  }

  &__filter {
    position: absolute;
    content: "";
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    transition: $g-anime;
  }

  &__more {
    opacity: 0;
    visibility: hidden;
    position: absolute;
    z-index: 5;
    top: 50%;
    left: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 180px;
    height: 50px;
    transform: translate(-50%, -50%);
    @include fluidFontSize(18px);
    color: #fff;
    font-weight: 500;
    transition: $g-anime;
    transition-delay: 0.3s;
    border: 1px solid transparent;
  }

  &__content {
    padding-top: 35px;
    padding-bottom: 40px;
    padding-left: 25px;
    padding-right: 25px;

    &_views {
      padding-left: 0;
      padding-right: 0;
      max-width: 700px;
    }
  }

  &__wrap-price {
    margin-bottom: 30px;
    color: $g-col-title;
  }

  &__price {
    @include fluidFontSize(26px, 32px);
    font-weight: 700;
  }

  &__price-for {
    @include fluidFontSize(14px, 18px);
  }

  &__table {
    width: 100%;
    opacity: 0.7;
  }

  &__tr {
    display: flex;
    padding: 5px 0;
  }

  &__td {
    width: 50%;
  }

  &__title {
    margin-bottom: 25px;
    @include fluidFontSize(18px);
    font-weight: 500;
    color: $g-col-title;
  }

  &__text {
    line-height: 1.5;
    margin-bottom: 0;
  }
}

@media (max-width: $breakpoint-medium) {
  .b-slider {
    padding-top: 30px;
    padding-bottom: 60px;
    &__wrap-dots {
      top: 15px;
      padding-left: 10px;
    }
  }
  .s-room {

    &__img {
      &:before, &:after {
        content: none;
      }
    }
    &__content {
      padding-top: 15px;
      padding-left: 0;
      padding-right: 0;
    }

    &__wrap-price {
      margin-bottom: 10px;
    }

    &__title {
      margin-top: 20px;
      margin-bottom: 15px;
    }
  }
}

@media (max-width: $breakpoint-small) {

  .b-slider {
    &_dark {
      padding-bottom: 20px;
    }
    &__content {
      padding-bottom: 20px;
    }

    &__controls {
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
    &__wrap-dots {
      position: relative;
      top: 0;
      right: 0;
      margin-left: 0;
    }

    &__wrap-nav {
      position: static;
    }

    &__link {
      text-align: left;
      margin-top: 30px;
    }
  }

  .s-room {
    &__content {
      padding-bottom: 0;

      &_views {
        padding-bottom: 20px;
      }
    }
  }
}


// slider offers 
.p-offers {
  margin-bottom: 130px;
  .uk-grid-margin {
    margin-top: 120px;
  }
}
.s-offers {
  color: #fff;

  &_page {
    color: $g-col-title;

    &:nth-child(4) {
      color: #fff;
      .s-offers__wrapper {
        position: relative;
      }

      .s-offers__img {
        margin-bottom: 0;
      }

      .s-offers__wrap-price, .s-offers__wrap-btn {
        display: none;
      }

      .s-offers__content {
        position: absolute;
        z-index: 5;
        top: 25%;
        left: 0;
        width: 100%;
        padding: 0 45px;
        text-align: center;
      }

      .s-offers__title {
        position: relative;
        @include fluidFontSize(20px, 30px);
        padding-bottom: 40px;
        &:before {
          position: absolute;
          content: "";
          bottom: 0;
          left: 50%;
          width: 70%;
          height: 1px;
          transform: translateX(-50%);
          background-color: #fff;
        }
      }

      .s-offers__img {
        position: relative;
        &:before {
          position: absolute;
          content: "";
          z-index: 0;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
          background-color: rgba(#000, 0.5);
        }
      }

      .s-offers__text {
        max-width: 100%;
        color: #fff;
        line-height: 1.5;
      }
    }

    .s-offers__text {
      max-width: 380px;
      color: $g-col-text;
    }

    .btn_dark {
      height: 60px;
      margin-right: 70px;
    }
  }

  &__img {
    margin-bottom: 30px;
  }
  
  &__wrap-price {
    display: flex;
    margin-bottom: 15px;
    @include fluidFontSize(20px, 30px);
    font-weight: 500;
  }

  &__price {
    margin: 0;
  }

  &__price-new {
    margin-right: 5px;
  }

  &__price-old {
    margin: 0;
    margin-right: 20px;
    text-decoration: line-through;
    color: #c4c4c4;
  }

  &__text {
    margin-bottom: 60px;
    @include fluidFontSize(12px, 14px);
    line-height: 1.5;
  }

  &__wrap-btn {
    display: flex;
    align-items: center;
  }

  .btn_transl {
    margin-right: 70px;
  }
}

@media (max-width: $breakpoint-medium) {
  .p-offers {
    .uk-grid-margin {
      margin-top: 60px;
    }
  }
  .s-offers {
    &_page {
      .s-offers__wrap-btn {
        margin-bottom: 0;
      }
      .btn_dark {
        margin-right: 30px;
      }

      &:nth-child(4){

