}

// preview 
.preview {
  &__img {
    &:first-child {
      margin-bottom: 10px;
    }
  }

  &__content {
    padding-left: 20px;
    padding-top: 30px;
  }

  &__title {
    max-width: 540px;
    color: $g-col-title;
    @include fluidFontSize(18px, 30px);
    margin-bottom: 50px;
    font-weight: 500;
  }

  &__text {
    max-width: 480px;
    margin-bottom: 100px;
    line-height: 1.5em;
  }

  &__list {
    display: flex;
    justify-content: space-between;
    margin-left: -20px;
  }

  &__item {
    width: 33.33%;
    padding-left: 20px;
    text-align: center;
    word-break: normal;
  }
  
  &__icon {
    margin-bottom: 30px;
  }

  &__subtitle {
    margin-left: auto;
    margin-right: auto;
    font-weight: 500;
  }
}

