.room {
  margin-top: 10px;
  margin-bottom: 120px;

  &__headline {
    @include fluidFontSize(26px, 32px);
    font-weight: 700;
    color: $g-col-title;
  }

  &__wrap-price {
    display: flex;
    align-items: baseline;
    margin-bottom: 10px;
    color: $g-col-title;
  }

  &__price {
    margin-right: 5px;
    @include fluidFontSize(26px, 32px);
    font-weight: 700;
    color: $g-col-title;
  }

  &__benef {
    margin-top: 0;
    margin-bottom: 30px;
    color: $g-col-title;
  }

  &__subtitle {
    margin-bottom: 10px;
    @include fluidFontSize(18px);
    font-weight: 500;
    color: $g-col-title;
  }

  &__item {
    display: flex;
    align-items: center;
    padding-top: 8px;
    padding-bottom: 8px;
    color: #A1ABAD;
  }

  &__icon {
    margin-right: 20px;
  }

  &__feature {
    margin: 0;
  }

  &__title {
    font-weight: 500;
    color: $g-col-title;
  }

  &__text {
    line-height: 1.5;
  }
}

@media (max-width: $breakpoint-small) {
  .room {
    margin-top: 20px;
    margin-bottom: 80px;
    &__content {
      display: flex;
      flex-direction: column;
    }
    &__headline {
      margin-bottom: 5px;
    }

    .book-fm {
      order: 3;
    }

    &__title {
      margin-top: 0;
      margin-bottom: 10px;
    }

    &__text {
      margin-top: 0;
      margin-bottom: 30px;
    }
  }
}