.hedr {
  padding-left: 30px;
  padding-right: 30px;
  color: rgba($g-col-main, 0.9);
  @include fluidFontSize($f-min: 14px,$w-def: 1440px, $w-max: 1440px);

  &__desk {
    min-height: 80px;
    display: flex;
    align-items: center;
  }

  &__content {
    width: 100%;
    margin-left: 15px;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &__wrap {
    display: flex;
    align-items: center;
  }

  &__contacts {
    .link {
      margin-left: auto;
      margin-bottom: 10px;
    }
  }

  &__wapp {
    display: inline-block;
    width: 20px;
    height: 20px;
  }

  &__phone {
    .icon {
      margin-right: 10px;
    }
  }

  &__soc-lang {
    margin-left: 50px;
  }
  
  &__social {
    margin-bottom: 10px;
    justify-content: flex-end;
    a {
      opacity: 0.5;

      &:hover {
        opacity: 1;
      }
    }
  }

  &__lang {
    display: flex;
    align-items: center;
    text-transform: uppercase;
  }

  &__bell {
    font-weight: 500;
    margin-right: 20px;
    .icon {
      margin-right: 15px;
    }
  }
}

.logo {
  margin-right: 15px;
}

.menu {

  @include fluidFontSize($f-min: 14px,$w-def: 1440px, $w-max: 1440px);
  
  &__list {
    display: flex;
    align-items: center;
  }

  &__link {
    padding: 10px;

    color: rgba($g-col-main, 0.9);

    &:hover {
      color: rgba($g-col-main, 0.5);
    }
  }
}

.lang {
  margin-left: -15px;
  margin-right: -15px;
  padding-left: 15px;
  padding-right: 15px;
  @include fluidFontSize($f-min: 14px,$w-def: 1440px, $w-max: 1440px);
  text-transform: uppercase;
  font-weight: 500;
  color: rgba($g-col-main, 0.9);

  &__btn {
    text-transform: uppercase;
    border: none;
    background-color: transparent;

    &.uk-open {
      z-index: 1050;
    }
  }

  &__popup {
    min-width: 65px;
    padding: 40px 8px 10px;
    color: rgba($g-col-main, 0.9);
  }

  &__item {
    display: block;

    padding: 7px 5px;
    &:first-of-type {
      padding-top: 15px;
      border-top: 1px solid rgba(#000, 0.05);
    }

    &:hover {
      opacity: 0.7;
    }
  }
}

.btn-book {
  position: fixed;
  z-index: 1100;
  right: 0;
  bottom: 20px;
  transform: rotate(180deg);

  .btn_dark {
    height: 140px;
    width: 32px;
    text-transform: uppercase;
    writing-mode: vertical-rl;
    @include fluidFontSize(14px);
  }
}

@media (max-width: $breakpoint-large) {

  .hedr {
    &__soc-lang {
      margin-left: 20px;
    }
  }

  .menu {
    &__link {
      padding: 7px;
    }
  }
}

@media (max-width: $breakpoint-medium) {
  .hedr {
    &__top {
      min-height: 54px;
      display: flex;
      align-items: center;
      justify-content: space-between;

      border-bottom: 1px solid rgba(#000, 0.05);
    }

    &__bottom {
      min-height: 56px;
      display: flex;
      align-items: center;
      justify-content: space-between;

      .btn_dark {
        width: 120px;
        height: 32px;
      }
    }

    &__phone {
      letter-spacing: 0.01em;
    }

    &__content {
      max-width: 240px;
      display: flex;
      align-items: center;
      margin-left: 25px;
    }
  }

  .logo {
    max-width: 135px;
  }

  .mob-menu {
    &__popup {
      padding: 0;
      background-color: transparent;
    }

    &__bg-btn {
      position: relative;
      margin-top: -10px;
      padding-top: 10px;
      overflow: hidden;

      div {
        width: 60px;
        height: 60px;
        background-color: #fff;
        box-shadow: 0px 10px 30px rgba(0, 0, 0, 0.15);
      }
    }
  }

  .menu {
    padding: 15px 20px;
    background-color: #fff;
    box-shadow: 0px 10px 30px rgba(0, 0, 0, 0.15);
    &__list {
      display: block;
    }

    &__link {
      display: block;
      padding: 15px 5px;
    }
  }
}

@media (max-width: $breakpoint-small) {

  .hedr {
    padding-left: 20px;
    padding-right: 20px;
  }
}