//--------------------------------
// Global variables
//--------------------------------
$g-font: 'Rubik', sans-serif;


$g-col-title: #242424;
$g-col-text: #757575;
$g-col-red: #D70C0C;
$g-col-main: #163F46;
$g-col-extra: #2D5258;

$g-shadow: 0px 12px 24px rgba(0, 0, 0, 0.06);
$g-anime: 0.3s cubic-bezier(0.25, 0.1, 0.25, 1);


$font-sizes: (
	//			960  1200
	h1: 	(30px, 45px)
);

//--------------------------------
// Global breakpoints
//--------------------------------
$breakpoint-small:                              640px !default;  // Phone landscape
$breakpoint-medium:                             960px !default;  // Tablet Landscape
$breakpoint-large:                              1200px !default; // Desktop
$breakpoint-xlarge:                             1600px !default; // Large Screens

$breakpoint-default:                             1210px !default; // default width

//--------------------------------
// Global mixins
//--------------------------------
@mixin fluidFontSize($f-min, $f-def: $f-min, $w-def:$breakpoint-default, $w-min: $breakpoint-medium, $w-max: $breakpoint-default) {

	$f-new-def: $f-def - ($w-def / 200);
	font-size: calc(#{$f-new-def} + 0.5vw);

	@media (max-width: $w-min) {
		font-size: $f-min;
	}
	@media (min-width: $w-max) {
		font-size: $f-def;
	}
}

@mixin font-size($sizes) {
	$sizesList: map-get($font-sizes, $sizes);
	$newFont: nth($sizesList, 2) - ($breakpoint-default / 200);

	font-size: calc(#{$newFont} + 0.5vw);

	@media (max-width: $breakpoint-medium) {
		font-size: nth($sizesList, 1);
	}
	@media (min-width: $breakpoint-default) {
		font-size: nth($sizesList, 2);
	}
};

//--------------------------------
// Global style
//--------------------------------
::placeholder {
	color: $g-col-text;
}

::selection {
	// background-color: $g-col-green;
	color: #fff;
}

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
	overflow: hidden; }

