		// border: 1px solid $g-col-gray-strouk;
		outline: none;
		&:focus:required:invalid {
			// border-color: $g-col-red;
		}
		&:required:valid {
			// border-color: $g-col-green;
		}
	}
}

.container {
	max-width: $breakpoint-default;
	margin: 0 auto;
	padding-left: 30px;
	padding-right: 30px; }

.container-max {
	max-width: $breakpoint-default;
	margin: 0 auto; }


.text-center {
	text-align: center;
}

b, strong {
	font-weight: bold;
}

ul {
	margin: 0;
	padding: 0;
	list-style: none;
}

a {
	color: inherit;
	transition: $g-anime;
	&:hover {
		text-decoration: none;
	}

	&:focus {
		outline: none;
	}

	&:before, &:after {
		transition: $g-anime;
	}
}

* { box-sizing: border-box; }

button {
	font-family: $g-font;
	font-size: inherit;
	font-weight: inherit;
	color: inherit;
	cursor: pointer;
	transition: $g-anime;
	padding: 0;
	&:before, &:after {
		transition: $g-anime;
	}
	&:focus {
		outline: none;
	}
}

input {
	width: 100%;
	padding-top: 12px;
	padding-bottom: 12px;
	padding-left: 30px;
	padding-right: 20px;
	font-family: $g-font;
	border: 1px solid #C4C4C4;
	border-radius: 5px;
	// color: $g-col-blue;
	// background: $g-col-l-gray;
	letter-spacing: 0.01em;
	&::placeholder {
		font-size: inherit;
		color: $g-col-text;
	}
}

select, textarea {
	font-family: $g-font;
}


.border-bm-dot {
	position: relative;
	padding-bottom: 2px;

	&:after {
		position: absolute;
		content: "";
		bottom: 0;
		left: 0;
		width: 100%;
		height: 1px;
		border-bottom: 1px dashed #BBC3C4;
		transition: $g-anime;
	}

	&:hover:after {
		border-color: transparent;
	}
}

