//--------------------------------
// here --> accord-arrow
//--------------------------------
.arrow-up, .arrow-right, .arrow-left, .arrow-down {
  position: relative;
  display: inline-block;
  padding-right: 20px;

  &:after {
    position: absolute;
    content: "";
    top: 50%;
    right: 3px;
    border: solid $g-col-main;
    border-width: 1px 1px 0 0;
    padding: 3px;
    transition: $g-anime;
		transform: translate(0, -85%) rotate(135deg);	
  }

  &.uk-open, &.active {
    &:after {
			transform: translate(0, -50%) rotate(-45deg);
    }
  }
}

.uk-open > .arrow-up:after, .active > .arrow-up:after {
	transform: translate(0, -50%) rotate(-45deg);	
}

.arrow-right {
	&:after {
		border-width: 2px 0 0 2px;
		transform: translate(0, -50%) rotate(135deg);	
  }
}

.arrow-left {
	padding-right: 0;
	padding-left: 20px;
	&:after {
    right: auto;
		left: 3px;
		border-width: 0 2px 2px 0;
		transform: translate(0, -50%) rotate(135deg);
  }
}


//--------------------------------
// here --> accord-arrow
//--------------------------------
.pointer {
	position: relative;
	display: inline-block;
	width: 21px;
	height: 20px;

	&:hover {
		opacity: 0.7;
	}

	&:before {
		position: absolute;
		content: "";
		top: 50%;
		transform: translateY(-50%);
		width: 20px;
		height: 2px;
		background-color: #fff;
	}

	&:after {
		position: absolute;
    content: "";
    top: 50%;
    // border: solid $g-col-white;
    padding: 3px;
    transition: $g-anime;
		transform: translate(0, -50%) rotate(135deg);	
	}

	&_left {
		&:before {
			left: 1px;

		}

		&:after {
			left: 0;
			border-width: 0 2px 2px 0;
		}
	}

	&_right {
		&:before {
			right: 1px;
		}

		&:after {
			right: 0;
			border-width: 2px 0 0 2px;
		}
	}
}


.triangle {
	position: relative;

	&:after {
		position: absolute;
		content: "";
		right: 0;
		top: 50%;
		transform: translateY(-50%);
		transition: $g-anime;
    border-left: 7px solid transparent;
    border-right: 7px solid transparent;
    // border-top: 12px solid $g-col-blue;
	}
}

.uk-open .triangle {
	&:after {
		border-top: none;
		// border-bottom: 12px solid $g-col-blue;
	}
}
//--------------------------------
// here --> icon cross
//--------------------------------
.cross {
	position: relative;
	&:before, &:after {
		position: absolute;
		content: "";
		top: 50%;
		right: 0;
		transform: translateY(-60%) rotate(-45deg);
		width: 10px;
		height: 2px;
		background: #fff;
	}

	&:after {
		transform: translateY(-60%) rotate(45deg);
	}
}


