}

@media (max-width: $breakpoint-small) {

	.container {
		padding-left: 20px;
		padding-right: 20px;
	}

	.headline {
		margin-top: 20px;
		margin-bottom: 20px;
	}

	.b-back {
		margin-top: 20px;
		margin-bottom: 20px;
	}

	.pagination {
		margin-top: 0;
		margin-bottom: 60px;
	}
}

